<template>
  <div class="activity">
    <div class="activity_header">
      <div class="active_banner common">
        <img src="../../assets/images/activity/activity_bg200.png" srcset="../../assets/images/activity/activity_bg300.png 3x" alt="">
      </div>
      <div class="packet common">
        <img class="bao" src="../../assets/images/activity/activity_packet_bg200.png" srcset="../../assets/images/activity/activity_packet_bg300.png 3x" alt="">
        <!--<img class="press" src="../../assets/images/activity/activity_packet_btn200.png" srcset="../../assets/images/activity/activity_packet_btn300.png 3x" alt="">-->
        <img @click="openRedPacket" v-if="notOpenPacket" class="press" src="../../assets/images/activity/activity_packet_btn.gif" alt="">
        <img v-if="!notOpenPacket" class="press" src="../../assets/images/activity/activity_packet_move.gif" alt="">
      </div>
      <div class="activity_answer common">
        <img class="answer" src="../../assets/images/activity/activity_answer_bg200.png" srcset="../../assets/images/activity/activity_answer_bg300.png 3x" alt="">
        <div class="press_container" @click="startAnswer">
          <img v-if="startBefore" class="press" src="../../assets/images/activity/activity_answer_btn200.png" srcset="../../assets/images/activity/activity_answer_btn300.png 3x" alt="">
          <img v-else class="press" src="../../assets/images/activity/activity_answer_btn2200.png" srcset="../../assets/images/activity/activity_answer_btn2300.png 3x" alt="">
        </div>
        <p>视小宝点赞祖国答题活动 15题时间10分钟，答对一题计一分 分数相同用时短者排名靠前</p>
      </div>
      <div class="activity_share common">
        <img class="bg" src="../../assets/images/activity/activity_share_bg200.png" srcset="../../assets/images/activity/activity_share_bg300.png 3x" alt="">
        <p>点击右上角分享，其他小伙伴领完红包后，您可获得随机红包奖励，分享越多，收入越多。</p>
        <span>点击右上角分享活动吧！</span>
      </div>
      <span class="bottom_text">活动时间：2019年10月01日至10月7日</span>
    </div>

    <div class="red_packet_dialog">
      <van-dialog
        v-model="showRedPacket"
        :show-cancel-button = "false"
        :showConfirmButton = "false"
      >
        <div>666</div>
      </van-dialog>
    </div>

    <div class="answer_dialog">
      <van-dialog
        v-model="showAnswer"
        :show-cancel-button = "false"
        :showConfirmButton = "false"
      >
        <div class="answer_bg">
          <img @click="delAnswer" class="del" src="../../assets/images/answer/answer_btn_colse200.png" srcset="../../assets/images/answer/answer_btn_colse300.png 3x" alt="">
          <div class="top">
            <span class="title">答题赢大奖</span>
            <span class="time">倒计时：{{countDownList}}</span>
            <i class="line"></i>
          </div>
          <div class="main">
            <div class="subject">
              <p>1、近期全国各地持续上演垃圾分类热潮，遇到分辨不清的垃圾您可以（ ）</p>
              <ul>
                <li>
                  <span>A、直接语音问视小宝 "XXX是什么垃圾"</span>
                </li>
                <li>
                  <span class="active">B、不分类直接扔到垃圾桶</span>
                </li>
                <li>
                  <span>C、厨余垃圾分类复杂，以后只去餐馆吃不在家做饭</span>
                </li>
              </ul>
            </div>
            <span class="next" @click="handleSubmit">下一题</span>
          </div>
        </div>
      </van-dialog>
    </div>

    <div class="answer_dialog_end">
      <van-dialog
        v-model="showAnswerEnd"
        :show-cancel-button = "false"
        :showConfirmButton = "false"
      >
        <div class="answer_bg">
          <div class="main">
            <span>温馨提示</span>
            <p>答题时间已到，系统将自动提交（{{subCountdown}}s）</p>
          </div>
        </div>
      </van-dialog>
    </div>
  </div>
</template>

<script>
// import {getNowFormatDate} from '../../utils/utils'
import wx from 'weixin-js-sdk'
const jsApiList = ['onMenuShareQQ', 'onMenuShareAppMessage', 'onMenuShareTimeline', 'updateAppMessageShareData', 'updateTimelineShareData']
// const jsApiList = ['onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ', 'onMenuShareWeibo']

var answerTimeCountDown
export default {
  name: 'activity',
  data () {
    return {
      showAnswer: false,
      showAnswerEnd: false,
      showRedPacket: false,
      notOpenPacket: true,
      subCountdown: 3,
      startBefore: true,
      countDownList: '00天00时00分00秒',
      actEndTime: ''
    }
  },
  created () {

  },
  mounted () {
    this.getConfig()
    this.link = location.origin + '/index.html#/share?openId=' + 666 + '&shareId=' + 777
    alert(this.link)
    this.desc = 'aaaaaaa'
    this.shareToFriends()
    this.shareToFriendsCircle()
  },
  methods: {
    // getQueryString(name) {
    //   var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    //   var t = window.location.href.split("=")[1].split("#")[0];
    //   window.localStorage.setItem('openid', t);
    // },

    getUrl () {
      if (window.entryUrl === '') {
        window.entryUrl = location.href.split('#')[0]
      }
      var u = navigator.userAgent
      var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
      console.log(isAndroid ? location.href.split('#')[0] : window.entryUrl)
      return isAndroid ? location.href.split('#')[0] : window.entryUrl
    },

    getConfig () {
      var url = this.getUrl()
      alert(url)
      // return new Promise((resolve, reject) => {
      //   this.$api.activity('', {
      //     params: {
      //       url: url
      //     }
      //   }).then((response) => {
      //     var data = response.data.data
      //     var appId = data.appId
      //     var noncestr = data.nonceStr
      //     var timestamp = data.timestamp
      //     var signature = data.signature
      //     wx.config({
      //       debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      //       appId: appId, // 必填，公众号的唯一标识
      //       timestamp: timestamp, // 必填，生成签名的时间戳
      //       nonceStr: noncestr, // 必填，生成签名的随机串
      //       signature: signature, // 必填，签名，见附录1
      //       jsApiList: jsApiList // 必填，需要使用的JS接口列表，所有JS接口列表 见附录2
      //     })
      //     wx.error(function (res) {
      //       console.log(JSON.stringify(res))
      //     })
      //     resolve()
      //   })
      // })
    },

    shareToFriendsCircle () {
      wx.ready(() => {
        wx.onMenuShareTimeline({
          title: '6666',
          link: 'https://www.baidu.com',
          imgUrl: 'https://www.baidu.com',
          success: function () {
           alert(1)
          }
        })
      })
    },

    shareToFriends () {
      wx.ready(() => {
        wx.onMenuShareAppMessage({
          title: '6666',
          link: 'https://www.baidu.com',
          imgUrl: 'https://www.baidu.com',
          desc: 'bbbb',
          success: function () {
            alert(2)
          }
        })
      })
    },

    startAnswer () {
      this.showAnswer = true
      this.startBefore = !this.startBefore
      this.actEndTime = new Date().getTime() + (10 * 60 * 1000)
      setTimeout(() => {
        this.countDown()
      }, 200)
    },
    delAnswer () {
      this.showAnswer = false
      this.startBefore = true
    },

    timeFormat (param) {
      return param < 10 ? '0' + param : param
    },
    countDown (it) {
      // console.log(getNowFormatDate())
      // console.log(new Date().toLocaleDateString())
      answerTimeCountDown = setInterval(() => {
        // 获取当前时间，同时得到活动结束时间数组
        let newTime = new Date().getTime() // 获取当前毫秒数 时间戳

        // 对结束时间进行处理渲染到页面 时间戳
        let endTime = new Date(this.actEndTime).getTime()

        let obj = null
        // 如果活动未结束，对时间进行处理
        if (endTime - newTime > 0) {
          let time = (endTime - newTime) / 1000
          // 获取天、时、分、秒
          let day = parseInt(time / (60 * 60 * 24))
          let hou = parseInt(time % (60 * 60 * 24) / 3600)
          let min = parseInt(time % (60 * 60 * 24) % 3600 / 60)
          let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60)
          obj = {
            day: this.timeFormat(day),
            hou: this.timeFormat(hou),
            min: this.timeFormat(min),
            sec: this.timeFormat(sec)
          }
        } else { // 活动已结束，全部设置为'00'
          this.showAnswer = false
          this.showAnswerEnd = true
          let second = 3
          const timer = setInterval(() => {
            second--
            if (second) {
              this.subCountdown = second
            } else {
              clearInterval(timer)
              this.submitAnswer()
            }
          }, 1000)

          obj = {
            day: '00',
            hou: '00',
            min: '00',
            sec: '00'
          }
          clearInterval(answerTimeCountDown)
        }
        this.countDownList = obj.day + '天' + obj.hou + '时' + obj.min + '分' + obj.sec + '秒'
      }, 1000)
    },

    /** 打开红包 */
    openRedPacket () {
      this.notOpenPacket = false
      this.showRedPacket = true
    },
    submitAnswer () {
      this.$toast('提交成功')
      setTimeout(() => {
        this.showAnswerEnd = false
      })
    },
    // 手动提交
    handleSubmit () {
      clearInterval(answerTimeCountDown)
      this.$toast(this.countDownList)
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .activity {
    width: 7.5rem;
    margin: 0 auto;
    height: 100%;
    /*background: #EE2C2B;*/
    background: -moz-linear-gradient(top, #EE2C2B 0%, #F49464 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#EE2C2B), color-stop(100%,#F49464));
    background: -webkit-linear-gradient(top, #EE2C2B 0%,#F49464 100%);
    background: -o-linear-gradient(top, #EE2C2B 0%,#F49464 100%);
    background: -ms-linear-gradient(top, #EE2C2B 0%,#F49464 100%);
    background: linear-gradient(to bottom, #EE2C2B 0%,#F49464 100%);
  }
  .activity_header {
    width: 100%;
    padding: .4rem .27rem 0 .27rem;
    /*@include bg-image('../../assets/images/activity/activity_bg2');
    background-repeat: no-repeat;
    background-position: center;*/
    @include fcc;
    .common {
      width: 100%;
      position: relative;
      @include fcc;
    }
    .active_banner {
      width: 100%;
      img {
        display: block;
        width: 100%;
      }
    }
    .packet {
      margin-top: -.22rem;
      .bao {
        display: block;
        width: 5.76rem;
      }
      .press {
        position: absolute;
        bottom: 1.2rem;
        width: 1.16rem;
        height: 1.25rem;
      }
    }
    .activity_answer {
      .answer {
        width: 100%;
      }
      p {
        position: absolute;
        top: 1.56rem;
        width: 4.7rem;
        @include sc(.28rem, $txtColr);
        line-height: .4rem;
        text-align: center;
        font-weight: bold;
      }
      .press_container {
        position: absolute;
        bottom: 1.4rem;
        width: 1.9rem;
        height: 1.37rem;
      }
      .press {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .activity_share {
      .bg {
        width: 100%;
      }
      p {
        position: absolute;
        top: 1.35rem;
        padding: 0 .6rem;
        @include sc(.28rem, $txtColr);
        line-height: .38rem;
      }
      span {
        position: absolute;
        bottom: .6rem;
        @include sc(.3rem, $titleColr);
      }
    }
    .bottom_text {
      @include sc(.3rem, $titleColr);
      font-weight: bold;
      padding-top: .62rem;
      padding-bottom: .76rem;
    }
  }
  .answer_dialog {
    width: 100%;
    padding: 0 .3rem;
    .answer_bg {
      position: relative;
      @include fcc;
      @include bg-image('../../assets/images/answer/answer_bg');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      width: 7rem;
      height: 10.56rem;
      .del {
        position: absolute;
        top: .25rem;
        right: .25rem;
        z-index: 999;
      }
      .top {
        @include fcc;
        width: 100%;
        padding: 0 .38rem;
        span {
          display: block;
        }
        .title {
          @include sc(.4rem, #DE4A36);
          line-height: .56rem;
          padding-top: .46rem;
          padding-bottom: .2rem;
        }
        .time {
          @include sc(.3rem, #FF7967);
          line-height: .42rem;
          padding-bottom: .25rem;
        }
        .line {
          display: block;
          width: 100%;
          height: 1px;
          border-bottom: dashed 1px #FB8C5F;
        }
      }
      .main {
        @include fcc;
        width: 100%;
        padding: .45rem;
        .subject {
          p {
            @include sc(.32rem, $txtColr);
            line-height: .52rem;
            font-weight: bold;
          }
          ul {
            padding-top: .2rem;
            li {
              padding: .2rem 0;
              span {
                display: block;
                background: #FFDA9C;
                padding: .26rem .3rem;
                border-radius: 1rem;
                @include sc(.3rem, $txtColr);
                line-height: .36rem;
                text-align: justify;
              }
              span.active {
                background: $titleColr;
                color: #fff;
              }
            }
          }
        }
        .next {
          position: absolute;
          bottom: .56rem;
          @include wh(3.5rem, .8rem);
          text-align: center;
          @include sc(.32rem, $titleColr);
          line-height: .8rem;
          -webkit-border-radius: 8px;
          -moz-border-radius: 8px;
          border-radius: 8px;
          border: solid 1px $titleColr;
        }
        .next:active {
          background: $titleColr;
          color: #fff;
        }
      }
    }
  }
  .answer_dialog_end {
    .answer_bg {
      position: relative;
      @include fcc;
      @include bg-image('../../assets/images/answer/answer_time');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      width: 6rem;
      height: 3.1rem;
      .main {
        @include fcc;
        width: 100%;
        span {
          @include sc(.34rem, $txtColr);
          line-height: .46rem;
          padding-top: .52rem;
          padding-bottom: .6rem;
        }
        p {
          @include sc(.3rem, $txtColr);
          line-height: .38rem;
        }
      }
    }
  }
</style>



